.container {
  .dateSearchFilter {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    margin-bottom: 1.2rem;

    .date {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      input,
      option,
      select {
        width: 100%;
        max-width: 400px;
        font-size: 1rem;
        padding: 0.8rem 0.5rem;
        background: rgb(236, 236, 236);
      }
      label {
        font-size: 1.1rem;
      }
    }

    .btn {
      align-self: flex-start;
      padding: 1rem;
      color: black;
      font-size: 1.1rem;
      background: var(--color-red-300);
      cursor: pointer;
      border-radius: 8px;
      transition: all 300ms ease-in-out;
      &:hover {
        background: hotpink;
        color: whitesmoke;
      }
    }
  }

  .w100 {
    width: 100%;
  }

  .results {
    margin-top: 1.2rem;
    .container {
      display: inline-block;
      @media screen and (max-width: 500px) {
        width: 100%;
        display: unset;
      }
      .deleteContainer {
        display: flex;
        align-items: center;
        gap: 2rem;
        margin-right: 2rem;
        margin-bottom: 2rem;
        width: 400px;
        @media screen and (max-width: 500px) {
          margin-right: 0;
          justify-content: space-between;
          width: unset;
        }
        .iconContainer {
          cursor: pointer;
          padding: 0.5rem;
          border-radius: 10px;
          border: 2px solid var(--color-red-300);
          display: flex;
          background: var(--color-red-100);
          transition: all ease-in-out 300ms;
          &:hover {
            background: whitesmoke;
          }
          svg {
            width: 25px;
            height: 25px;
            color: var(--color-red-600);
          }
        }
      }
    }
  }
}
